import React, { FC } from 'react';
import { Link } from 'gatsby';

import { NavItemProps } from './NavItemProps';

export const MobileNavItem: FC<NavItemProps> = ({
  title,
  basePath,
  isNotALink,
  items,
}) => {
  return (
    <div className={'my-1 border-b border-emerald-green-light text-jet-black'}>
      <div
        onClick={() => {
          if (!isNotALink) window.location.href = basePath;
        }}
      >
        <p className={'my-1 font-semibold'}>{title}</p>
      </div>

      <div className={'ml-3'}>
        {items?.map(({ path, child }, index) => (
          <div key={index} className={'my-1 text-sm text-jet-black-light'}>
            <Link
              key={index}
              to={`${basePath}${path}`}
              style={{
                textDecoration: 'none',
                color: 'inherit',
                width: '100%',
                padding: '0.8rem 0',
              }}
            >
              {child}
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
};
